
import { MainLayout } from '@/components/layout/main-layout';
import { ChatListItem } from '@/components/chat/chat-list-item';
import { mockChats, getCurrentUser } from '@/lib/data';
import type { Chat } from '@/lib/types';
import { Input } from '@/components/ui/input';
import { Search, MessageCircle } from 'lucide-react'; // Corrected import for MessageCircle

export default function ChatsPage() {
  const chats: Chat[] = mockChats; // In a real app, fetch this data
  const currentUser = getCurrentUser();

  // Sort chats by the timestamp of their last message, most recent first
  const sortedChats = [...chats].sort((a, b) => {
    const lastMessageATime = a.lastMessage?.timestamp || 0;
    const lastMessageBTime = b.lastMessage?.timestamp || 0;
    return lastMessageBTime - lastMessageATime;
  });

  return (
    <MainLayout title="Conecta Aí">
      <div className="p-3 bg-card sticky top-0 z-10 border-b">
        <div className="relative">
          <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-5 w-5 text-muted-foreground" />
          <Input type="search" placeholder="Search chats..." className="pl-10 h-10 rounded-full bg-muted border-none focus-visible:ring-primary" />
        </div>
      </div>
      <div className="flex flex-col bg-card">
        {sortedChats.length > 0 ? (
          sortedChats.map((chat) => (
            <ChatListItem key={chat.id} chat={chat} currentUser={currentUser} />
          ))
        ) : (
          <div className="flex flex-col items-center justify-center h-full p-8 text-center">
            <MessageCircle className="w-16 h-16 text-muted-foreground mb-4" />
            <h2 className="text-xl font-semibold text-foreground mb-2">No Chats Yet</h2>
            <p className="text-muted-foreground">Start a new conversation from your contacts.</p>
          </div>
        )}
      </div>
    </MainLayout>
  );
}
